import React, {useEffect, useState} from 'react';
import {isMobileOnly} from 'react-device-detect';
import { useTranslation } from 'react-i18next';
import Footer from "../components/Footer";
import Header from "../components/Header";
import './Main.css';

const Main = () => {
    const {t} = useTranslation();
    const [animateNav, setAnimateNav] = useState(false);
    const [animateBody, setAnimateBody] = useState(false);
    const [animateFooter, setAnimateFooter] = useState(false);

    useEffect(() => {
        setTimeout(() => {
            setAnimateNav(true)
            setTimeout(() => {
                setAnimateBody(true)
                setTimeout(() => {
                    setAnimateFooter(true)
                }, 500)
            }, 500)
        }, 500)
    }, [])

    return (
        
        <React.Fragment>
            <Header/>
            <main className='hero inner-height'>
                <section className={`hero-content fade ${animateBody ? 'in' : ''}`}>
                    {isMobileOnly &&
                        <div className='nav-link-logo mobile'/>
                    }
                    <div className="content-nav">
                        <div className="image-content"></div>
                        <div className="information-content">
                            <p>Esports tournament</p>
                            <h1>Arcane Masters Winter Series</h1>
                        </div>
                    </div>
                </section>
            </main>
            <Footer animation={animateFooter}/>
        </React.Fragment>
    )
}

export default Main;



